class FilterDataField extends React.Component{
    constructor() {
        super();
        updateFilterVal = updateFilterVal.bind(this)
        this.state = {
            fromDate: null,
            toDate: null
        };
    }


    handleChange = (id, date) => {
        if(id === "fromDate"){
            this.setState({
                fromDate: date
            });
            updateFilterVal("fromDate", date)
        }
        if(id === "toDate"){
            this.setState({
                toDate: date
            });
            updateFilterVal("toDate", date)
        }
    };

    render() {
        return (
            <div>
                Filter by date
                <br/>
                From:
                <DateRangePicker
                    startDate={this.state.fromDate} // momentPropTypes.momentObj or null,
                    startDateId="your_unique_start_date_id" // PropTypes.string.isRequired,
                    endDate={this.state.toDate} // momentPropTypes.momentObj or null,
                    endDateId="your_unique_end_date_id" // PropTypes.string.isRequired,
                    onDatesChange={({ fromDate, toDate }) => this.setState(fromDate, toDate)} // PropTypes.func.isRequired,
                    focusedInput={this.state.focusedInput} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                    onFocusChange={focusedInput => this.setState({ focusedInput })} // PropTypes.func.isRequired,
                />

                <DatePicker
                    className="dateFilter"
                    dateFormat="dd.MM.yyyy"
                    selected={this.state.fromDate}
                    onChange={(date) => this.handleChange("fromDate", date)}
                    showYearDropdown
                    showMonthDropdown
                />
                <br/>
                To:
                <DatePicker
                    className="dateFilter"
                    dateFormat="dd.MM.yyyy"
                    selected={this.state.toDate}
                    onChange={(date) => this.handleChange("toDate", date)}
                    showYearDropdown
                    showMonthDropdown
                />
            </div>
        )
    }
}


function updateFilterVal(key, newValue){
    if(key === "filterText")
        this.setState({filterText: newValue})

    if(key === "fromDate")
        this.setState({fromDate: newValue})

    if(key === "toDate")
        this.setState({toDate: newValue})
}


class FilterField extends React.Component{
    constructor(props){
        super(props);
        this.state = {filterText: ""}
    }

    handleChange(event) {
        this.setState({filterText: event.target.value});
        updateFilterVal("filterText", event.target.value)
    }

    render() {
        return (
            <div>
                Filter by contents of email and city:
                <input type="text" value={this.state.filterText} onChange={(e) => this.handleChange(e)}/>
            </div>
        );
    }
}


class ArchiveGetter extends React.Component{

    downloadDump = () => {
          // fake server request, getting the file url as response
        setTimeout(() => {
            const fd = this.state.fromDate
            const td = this.state.toDate
            const response = {
                file: "/download?from="+ ("0"+fd.getDate()).slice(-2)+("0"+(fd.getMonth()+1)).slice(-2)+fd.getFullYear() +
                    "&to="+ ("0"+td.getDate()).slice(-2)+("0"+(td.getMonth()+1)).slice(-2)+td.getFullYear(),
            };
            // server sent the url to the file!
            // now, let's download:
            window.open(response.file);
            // you could also do:
            // window.location.href = response.file;
        }, 100);
    }

    state = {
        fromDate: new Date(),
        toDate: new Date()
    };

    handleChange = (id, date) => {
        if(id === "fromDate"){
            this.setState({
                fromDate: date
            });
        }
        if(id === "toDate"){
            this.setState({
                toDate: date
            });
        }
    };

    render() {
        return (
            <div>
                Get archive entries by date
                <br/>
                From:
                <DatePicker
                    dateFormat="dd/MM/yyyy"
                    selected={this.state.fromDate}
                    onChange={(date) => this.handleChange("fromDate", date)}
                    showYearDropdown
                    showMonthDropdown
                />
                <br/>
                To:
                <DatePicker
                    dateFormat="dd/MM/yyyy"
                    selected={this.state.toDate}
                    onChange={(date) => this.handleChange("toDate", date)}
                    showYearDropdown
                    showMonthDropdown
                />
                <br/>
                <button onClick={this.downloadDump}>
                    click me
                </button>
            </div>
        )
    }
}


class ListElem extends React.Component {
    componentDidMount() {
    fetch("/api/getList"
    )
        .then(res => res.json())
        .then(
        (result) => {
            this.setState({
                isLoaded: true,
                items: result.items,
                donationsPDay: result.donations_per_day,
                filterText: ""
            });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
            this.setState({
                isLoaded: true,
                filterText: "",
                error
            });
        }
      )
    }


    constructor(props) {
        super(props);
        this.state = {
            items: [[3093, "15.03.2020", "14:43:56", "tchurovtim@2gmail.com", "Санкт-Петербург", "200"], [2, "15.03.2020", "14:43:56", "tchurovtim@gmail.com", "Санкт-Петербург", "200"]],
            donationsPDay: {"15.03.2020": {"donations_that_day": 2,
                                           "sum_that_day": 400}
            },
            filterText: "",
            fromDate: "",
            toDate: ""
        }
        updateFilterVal = updateFilterVal.bind(this)
    }

    convertDate(dateString) {
        console.log("dateStr", dateString);
        var parts = dateString.split('.');
        return new Date(parts[2], parts[1] - 1, parts[0]);
    }

    checkDateFilter(currentDate, fromDate, toDate){
        console.log("checkDF", this.convertDate(currentDate), fromDate, toDate)
        return fromDate < this.convertDate(currentDate) && this.convertDate(currentDate) < toDate
    }

    render() {
        var filteredItems = this.state.items.filter(item => item[1].includes(this.state.filterText) || item[3].includes(this.state.filterText) || item[4].includes(this.state.filterText))
        console.log("checking", filteredItems[0][1], this.state.fromDate, this.state.toDate)
        if(this.state.fromDate && this.state.toDate) {
            filteredItems = filteredItems.filter(item => this.checkDateFilter(item[1], this.state.fromDate, this.state.toDate))
        }
        return (
            <div>
                <table>
                    <tbody>
                {
                    filteredItems.map((item, index) => <DonationElem parentState={this.state}
                                                                    string={item} key={index} />)
                }
                    </tbody>
                </table>
            </div>
        );
    }
}


class DonationElem extends React.Component {

    displayRow(row){
        return row.map((cell) => <td key={this.props.key + cell.toString()}>{cell}</td>)
    }


    displaySpan(row){
        if(row === this.props.parentState.items.find((i) => i[1] === row[1])){
            let day_don= this.props.parentState.donationsPDay[row[1]]
            return <td rowSpan={day_don["donations_that_day"]}>{day_don["sum_that_day"]}</td>;
        }
    }


    render(){
        return(
            <React.Fragment>
                {<tr key={this.props.key}>{this.displayRow(this.props.string)}
                    {this.displaySpan(this.props.string)}
                </tr>}
            </React.Fragment>
        );
    }

}


export default App;
